<template>
  <div class="home">
    <el-menu
      :default-active="activeIndex"
      class="el-menu-demo"
      mode="horizontal"
      @select="handleSelect"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#ffd04b"
    >
      <el-menu-item index="1">Staging</el-menu-item>
      <el-menu-item index="2">MyPage</el-menu-item>
    </el-menu>
    <div class="line"></div>

    <div class="panel" v-if="panel=='1'">
      <div class="add">
        <div class="title">Add Server</div>
        <div class="form">
          <el-form :inline="true" class="demo-form-inline">
            <el-form-item label="Server-Name">
              <el-input v-model="servername" clearable></el-input>
            </el-form-item>
            <el-form-item label="Ip">
              <el-input v-model="ip" clearable></el-input>
            </el-form-item>
            <el-form-item label="Proportion">
              <el-input v-model="proportion" clearable></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="onSubmit">Add</el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>

      <div class="data">
        <div class="table">
          <el-table :data="tableData" style="width: 100%">
            <el-table-column prop="server" label="Server-Name" width="180"></el-table-column>
            <el-table-column prop="ip" label="Ip" width="180"></el-table-column>
            <el-table-column prop="proportion" label="Proportion" width="120"></el-table-column>
            <el-table-column label="Opration" width="80">
              <template slot-scope="scope">
                <div class="btns">
                  <div
                    class="del_btn"
                    v-bind:data-server="scope.row.server"
                    v-bind:data-ip="scope.row.ip"
                    v-bind:data-index="scope.$index"
                    @click="del"
                  >Del</div>
                  <el-popover placement="right" width="200" trigger="click">
                    <el-input v-model="newServerName" disabled class="input_style"></el-input>

                    <el-input placeholder="input ip" v-model="newIp" clearable class="input_style"></el-input>

                    <el-input
                      placeholder="input proportion"
                      v-model="newProportion"
                      clearable
                      class="input_style"
                    ></el-input>
                    <el-button type="primary" @click="changeConfirm" class="login_style">确定</el-button>
                    <div
                      class="del_btn"
                      slot="reference"
                      v-bind:data-server="scope.row.server"
                      v-bind:data-ip="scope.row.ip"
                      v-bind:data-prop="scope.row.proportion"
                      v-bind:data-index="scope.$index"
                      @click="change"
                    >Edit</div>
                  </el-popover>
                </div>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
    </div>

    <div class="panel" v-if="panel=='2'">工作台</div>
  </div>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      activeIndex: "1",
      panel: "1",
      servername: "",
      ip: "",
      proportion: "0",
      tableData: [],
      newServerName: "",
      newIp: "",
      newProportion: "0",
      oldServerName: "",
      oldIp: "",
      oldProportion: "0",
      newIndex: -1
    };
  },
  created() {
    let that = this;
    this.$axios
      .get("/getAll")
      .then(function(res) {
        console.log(res);
        if (res.status == 200) {
          that.tableData = res.data.data;
        } else {
          that.$notify({
            title: "data err!",
            message: "get data error!"
          });
        }
      })
      .catch(function(error) {
        that.$notify({
          title: "data err!",
          message: "get data error!"
        });
        console.log(error);
      });
  },
  methods: {
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
      if (key == 1) {
        this.panel = "1";
      } else {
        this.panel = "2";
      }
    },
    del(e) {
      var that = this;
      let server = e.target.getAttribute("data-server");
      let ip = e.target.getAttribute("data-ip");
      let index = e.target.getAttribute("data-index");

      this.$axios
        .delete("/del?server=" + server + "&ip=" + ip)
        .then(function(res) {
          if (res.status == 200 && res.data.success == true) {
            that.$notify({
              title: "success!",
              message: "delete server success!"
            });
            that.tableData.splice(index, 1);
          } else {
            that.$notify({
              title: "action err!",
              message: "delete server failure!"
            });
          }
        })
        .catch(error => {
          that.$notify({
            title: "action err!",
            message: "delete server failure!"
          });
          console.log(error);
        });
    },
    onSubmit() {
      var that = this;
      if (this.servername == "" && this.ip == "") {
        that.$notify({
          title: "input data err!",
          message: "servername and ip not be null!"
        });
        return;
      }
      for (var i in that.tableData) {
        if (
          that.tableData[i].server == this.servername.trim() &&
          that.tableData[i].ip == this.ip.trim()
        ) {
          that.$notify({
            title: "success!",
            message: "server already exsist!"
          });
          return;
        }
      }
      this.$axios
        .post("/add", {
          servername: this.servername.trim(),
          ip: this.ip.trim(),
          proportion: this.proportion
        })
        .then(function(res) {
          if (res.data.success == true && res.status == 200) {
            that.tableData.push({
              server: that.servername,
              ip: that.ip,
              proportion: that.proportion
            });
            that.servername = "";
            that.ip = "";
            that.proportion = 0;
            that.$notify({
              title: "success!",
              message: "add server success!"
            });
          } else {
            that.$notify({
              title: "action err!",
              message: "add server failure!"
            });
            that.servername = "";
            that.ip = "";
            that.proportion = 0;
          }
        })
        .catch(function(error) {
          console.log(error);
          that.$notify({
            title: "action err!",
            message: "add server failure!"
          });
          that.servername = "";
          that.ip = "";
          that.proportion = "0";
        });
    },
    change(e) {
      var that = this;
      let server = e.target.getAttribute("data-server");
      let ip = e.target.getAttribute("data-ip");
      let prop = e.target.getAttribute("data-prop");
      let index = e.target.getAttribute("data-index");
      this.newServerName = server;
      this.newIp = ip;
      this.newProportion = prop;
      this.oldServerName = server;
      this.oldIp = ip;
      this.oldProportion = prop;
      this.newIndex = index;
    },
    changeConfirm() {
      let that = this;
      this.$axios
        .post("/change", {
          newServerName: that.newServerName,
          newIp: that.newIp,
          newProportion: that.newProportion,
          oldServerName: that.oldServerName,
          oldIp: that.oldIp,
          oldProportion: that.oldProportion
        })
        .then(function(res) {
          if (res.data.success == true && res.status == 200) {
            that.tableData[that.newIndex] = {
              server: that.newServerName,
              ip: that.newIp,
              proportion: that.newProportion
            };
            that.$notify({
              title: "success!",
              message: "update server success!"
            });
            that.newServerName = "";
            that.newIp = "";
            that.newProportion = "";
            that.oldServerName = "";
            that.oldIp = "";
            that.oldProportion = "";
            that.newIndex = "";
          } else {
            that.newServerName = "";
            that.newIp = "";
            that.newProportion = "";
            that.oldServerName = "";
            that.oldIp = "";
            that.oldProportion = "";
            that.newIndex = "";
            that.$notify({
              title: "action err!",
              message: "update server failure!"
            });
          }
        })
        .catch(function(error) {
          console.log(error);
          that.newServerName = "";
          that.newIp = "";
          that.newProportion = "";
          that.oldServerName = "";
          that.oldIp = "";
          that.oldProportion = "";
          that.newIndex = "";
          that.$notify({
            title: "action err!",
            message: "update server failure!"
          });
        });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.add {
  display: flex;
  display: inline-block;
  flex-direction: column;
  margin-top: 15px;
}
.input_style {
  width: 200px;
}
.table {
  display: inline-block;
  margin: 10px auto;
}

.title {
  font-size: 18px;
  text-align: left;
  margin-bottom: 5px;
}

.btns {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.del_btn {
  text-decoration: underline;
}
</style>



