<template>
  <div class="login">
    <h1>Login</h1>
    <div>
      <el-input placeholder="enter username" v-model="name" clearable class="input_style"></el-input>
    </div>

    <div>
      <el-input placeholder="enter password" v-model="pwd" show-password class="input_style"></el-input>
    </div>
    <div>
      <el-button type="primary" @click="login" class="login_style">login</el-button>
    </div>
  </div>
</template>

<script>

export default {
  name: "Login",
  data() {
    return {
      name: "",
      pwd: ""
    };
  },
  methods: {
    login() {
      var that = this;
      this.$axios.post("/login", {
          username: this.name,
          password: this.pwd
        }).then(function(res) {
          if(res.data.success == true && res.status == 200){
            that.$store.commit('setLogin', true);
            localStorage.setItem('login', 'true');
           that.$router.push({ path: '/home' }, onComplete => { }, onAbort => { })
          } else {
            that.$notify({
              title: 'login err!',
              message: 'username or password error!'
            });
          }
        }).catch(function(error) {
          console.log(error);
        });
    }
  }
};
</script scoped>

<style>
.inlne {
  display: flex;
  flex-direction: column;
}
.login {
  margin-top: 200px;
}
.input_style {
  width: 200px;
  margin-bottom: 10px;
}
.login_style {
  width: 200px;
}
</style>


