// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import axios from 'axios'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import router from './router'
import store from './store/store'
Vue.use(ElementUI);
Vue.prototype.$axios = axios 
Vue.config.productionTip = false

router.beforeEach((to, from, next) => {
  let getFlag = localStorage.getItem('login') /* 这里是判断用户是否登录过，因为在用户登录后会在localStroage内存储Flag=isLogin */
  console.log(getFlag)
  if (getFlag == 'true') { /* 如果存在Flag并且为isLogin意味着用户登录，这时修改vux内state下isLogin的状态 */
    store.state.isLogin = true
    console.log("next()")
    next()
    if (!to.meta.isLogin) {
      console.log("next(/)") /* 如果在有登录状态的情况下前往不需要权限的路由路径，则判定为退出登录，进行提示并跳转登录页 */
      next({
        path: '/'
      })
    }
  } else {
    if (to.meta.isLogin) { /* 如果没有登录状态且要去往需要权限的路径时跳转登录页并进行提示 */
      next({
        path: '/'
      })
    } else {
      next()
    }
  }
})

router.afterEach(route => {
  window.scroll(0, 0)
})


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



