import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

export default new Vuex.Store({

  state: {
    isLogin: false
  },

  mutations: {
    setLogin(state, login) {
      state.isLogin = login
    }

  },

  getters: {

    //get方法

    getLogin: state => state.isLogin

  },

  actions: {

  },

  modules: {

  }

})



